import axios from 'axios';
import store from '../store';
import { loadTrackings, updateTracker } from '../actions';

const trackers = {
  loadData() {
    axios.get(`${__CONFIG__.API_URL}/api/trackers`)
      .then((response) => {
        console.log(response);
        store.dispatch(loadTrackings(response.data));
      })
      .catch((error) => {
        // TODO Add logger
        console.log(error);
      });
  },
  addTracking(id) {
    axios.post(`${__CONFIG__.API_URL}/api/trackers/track`, { id })
      .then((response) => {
        console.log(response);
        store.dispatch(updateTracker({ count: response.data.count, id }));
      })
      .catch((error) => {
        // TODO Add logger
        console.log(error);
      });
  },
};

export default trackers;
